/* LoadingSpinner.module.css */

.spinnerContainer {
  display: flex;
  justify-content: center;
  align-items: center;

}

.spinner {
  width: 1.6em;
  /*   */
  height: 1.6em;
  /*   */
  border: 0.25em solid white;
  /*    (        ) */
  border-top-color: var(--importantcolor);
  /*    ( ) */
  border-radius: 50%;
  /*      */
  animation: spin 1s linear infinite;
  /*   */
}


@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}